import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"

import Seo from "../components/seo"
import About from "../components/construct/About"
import Hire from "../components/construct/Hire"


export const Head = () => <Seo title="Sobre nosotros" />

const AboutUsPage = ({ data }) => {


  return (
    <Layout>
      <About />
      <Hire />
    </Layout>
  )
}

export const query = graphql`
  query {

    servicios: allServiciosJson {
      nodes {
        title
        description
        href
        image {
          childImageSharp {
            gatsbyImageData(
                placeholder: BLURRED
                formats: [AUTO, WEBP, AVIF]
                width: 370
                height: 188
              )
          }
        }
      }
    }
  
 
    allPagesJson(filter: { idPage: { eq: "index" } }) {
      nodes {
        idPage
        content {
          category
          description
          title
        }
      }
    }
  }
`

export default AboutUsPage
